  border-radius: 10px;
  transition: all 0.3s linear;
  transition: var(--transition);
}
.homeHero .homeHeroItem .link:hover {
  background: rgb(252, 194, 6);
  background: var(--clr-color);
  color: hsl(205, 77%, 27%);
  color: var(--clr-primary-2);
