/* SLIDER */
.gallery {
  background: crimson;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
  margin: auto;
  overflow: hidden;
  position: relative;
  width: 90%;
  height: 200px;
}
.gallery::before,
.gallery::after {
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  content: "";
  height: 100px;
  position: absolute;
  width: 100px;
  height: 200px;
  z-index: 2;
}
.gallery::after {
  right: 0;
  top: 0;
  -webkit-transform: rotateZ(180deg);
          transform: rotateZ(180deg);
}

.gallery::before {
  left: 0;
  top: 0;
}
.slide-track {
  -webkit-animation: scroll 40s linear infinite;
          animation: scroll 40s linear infinite;
  display: flex;
  width: calc(250px * 14);
}
.slide-track .gall {
  width: 200px;
  height: 300px;
}
.slide-track .gall img {
  height: 200px;
  width: 250px;
  object-fit: cover;
}
@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(calc(-250px * 7));
            transform: translateX(calc(-250px * 7));
  }
}
@keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(calc(-250px * 7));
            transform: translateX(calc(-250px * 7));
  }
}
.portfolioOverlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  overflow: hidden;
  width: 100%;
  height: 0;
  border-radius: 5%;
  transition: all 0.5s ease;
}
.styles-section .style .style-item:hover .portfolioOverlay {
  height: 100%;
}
.overlay-btn {
  background: transparent;
  color: crimson;
  border: 1px solid crimson;
  padding: 1rem;
  font-size: 17px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  transition: all 0.3s ease;
}
.overlay-btn:hover {
  background: crimson;
  color: #fff;
}
.gallery-page {
  color: white;
  background: crimson;
  padding: 1rem 1.2rem;
  margin-top: 1rem;
  border: 1px solid crimson;
  transition: all 0.5s ease;
}
.gallery-page:hover {
  background: transparent;
  color: crimson;
}
@media screen and (min-width: 800px) {
  /* HAIR STYLE */
  .styles-section {
    height: auto;
    margin-top: 3rem;
  }
  .styles-section .style {
    flex-direction: row;
  }

  .styles-section .style .style-item {
    width: 300px;
    margin: 3rem 0;
  }

  /* SLIDER */
  .gallery {
    width: 80%;
  }
  .gallery::before,
  .gallery::after {
