  }

  .slide-track .gall {
    width: 200px;
    height: 300px;
  }
}
/* SLIDER END */

/* ===============
     Reviews
=================== */
.reviewers-section {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: white;
}
.title {
  text-align: center;
  margin-bottom: 4rem;
}
.underline {
