.media-icons a:nth-child(2) {
  background: #1da1f2;
}
.media-icons a:nth-child(2):hover {
  background: #fff;
  color: #1da1f2;
}
.media-icons a:nth-child(3) {
  background: #e13060;
}
.media-icons a:nth-child(3):hover {
  background: #fff;
  color: #e13060;
}
.media-icons a:nth-child(4) {
  background: #0077b5;
}
.media-icons a:nth-child(4):hover {
  background: #fff;
  color: #0077b5;
}
.media-icons a:nth-child(5) {
  background: #ff0000;
}
.media-icons a:nth-child(5):hover {
  background: #fff;
  color: #ff0000;
}

.link-boxes {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.link-boxes .box {
  width: calc(100% / 5 - 10px);
}

.link_name {
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 10px;
  position: relative;
  text-transform: capitalize;
}

.link_name:before {
  /* content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  width: 35px;
  background: #fff; */
}

.box li {
  margin: 6px 0;
  list-style-type: none;
}
.box li a {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  opacity: 0.8;
  transition: all 0.4s ease;
  position: relative;
}
.box li a:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background: #fff;
  transition: all 0.3s ease-in-out;
}
.box li a:hover:before {
  width: 100%;
}

.input-box {
  margin-right: 55px;
}
.input-box input {
  height: 40px;
  width: calc(100% + 55px);
  outline: none;
  border: 1px solid #fff;
  background: #140b5c;
  border-radius: 4px;
  padding: 0 15px;
  font-style: 15px;
  color: #fff;
  margin-top: 5px;
}

.input-box input::-webkit-input-placeholder {
  color: #afafb6;
  font-size: 16px;
}

.input-box input:-ms-input-placeholder {
  color: #afafb6;
  font-size: 16px;
}

.input-box input::placeholder {
  color: #afafb6;
  font-size: 16px;
}
.input-box input[type="button"] {
  background: #fff;
  color: #140b5c;
  border: none;
  font-size: 18px;
  font-weight: 500;
  margin: 4px 0;
  opacity: 0.8;
  cursor: pointer;
  transition: all 0.4s ease;
}
.input-box input[type="button"]:hover {
  opacity: 1;
}

.bottom-details {
  width: 100%;
  background: #0f0844;
}

.bottom_text {
  max-width: 1250px;
  margin: auto;
  padding: 20px 40px;
  display: flex;
  justify-content: space-between;
}
.bottom_text span,
.bottom_text a {
  font-size: 14px;
  font-weight: 300;
  color: #fff;
  opacity: 0.8;
  text-decoration: none;
}
.bottom_text a:hover {
  opacity: 1;
  text-decoration: underline;
}
.bottom_text a {
  margin-right: 10px;
}

/*  Media Query */
@media (max-width: 900px) {
  .link-boxes {
    flex-wrap: wrap;
  }
  .input-box {
    width: 60%;
    margin-top: 10px;
  }
}
@media (max-width: 700px) {
  footer {
    position: relative;
  }
  .logo-details {
    font-size: 26px;
  }
  .media-icons a {
    height: 35px;
    width: 35px;
    font-size: 14px;
    line-height: 35px;
  }
  footer .content .link-boxes .box {
    width: calc(100% / 3 - 10px);
  }
  footer .content .link-boxes .input-box {
    width: 60%;
  }
  .bottom_text span,
  .bottom_text a {
    font-style: 12px;
  }
}

@media (max-width: 520px) {
  footer:before {
    top: 145px;
  }
  footer .content .top {
    flex-direction: column;
  }
  .top .media-icons {
    margin-top: 16px;
  }
  footer .content .link-boxes .box {
    width: calc(100% / 2 - 10px);
  }
  footer .content .link-boxes .input-box {
    width: 100%;
  }
}

