.post {
  width: 385px;
  margin: 0 25px 40px 25px;
}
.postImg {
  width: 100%;
  height: 280px;
  border-radius: 7px;
  object-fit: cover;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.postCat {
  font-family: "Varela Round", sans-serif;
  font-style: 11px;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
}
.postDate {
  font-family: "Lora", sans-serif;
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 15px;
}
.postDesc {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
